import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { listItemStyles } from './listItemStyle';
import { ListItemProperties, ListItemSlot } from './listItemProperties';
import down from '@umetrics/sartorius-ui-icon/dist/icons/down';

@defineElement({
    tagName: 'list-item',
    styles: listItemStyles,
    version,
    //    sub: [Icon, Button]
})
export class ListItem extends BaseElement implements ListItemProperties {
    // Static members
    private static Button;
    private static Icon;

    private static animations = {
        showIcon: {
            showAnimation: [
                {
                    opacity: '0',
                    transform: 'scale(0.8)',
                },
                {
                    opacity: '1',
                    transform: 'scale(1)',
                },
            ],
            hideAnimation: [
                {
                    opacity: '1',
                    transform: 'scale(1)',
                },
                {
                    opacity: '0',
                    transform: 'scale(0.8)',
                },
            ],
        },
        showLabel: {
            showAnimation: [
                {
                    width: '0px',
                },
                {
                    width: '70px',
                },
                {
                    width: 'fit-content',
                },
            ],
            hideAnimation: [
                {
                    width: 'fit-content',
                },
                {
                    width: '70px',
                },
                {
                    width: '0px',
                },
            ],
        },
    };

    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, attribute: true, reflect: true, converter: booleanConverter })
    clickable = false;
    @property({ type: Boolean, attribute: true, reflect: true, converter: booleanConverter })
    selected = false;
    @property({ type: Boolean, attribute: true, reflect: true, converter: booleanConverter })
    expanded = false;
    @property({ type: Boolean, attribute: true, reflect: true, converter: booleanConverter })
    disabled = false;
    @property({ type: Boolean, attribute: 'hidden-button', reflect: true, converter: booleanConverter })
    hiddenButton = false;
    @property({ type: Boolean, attribute: 'hidden-icon-buttons', reflect: true, converter: booleanConverter })
    hiddenIconButtons = false;
    @property({ type: Boolean, converter: booleanConverter })
    hasChildren = false;
    @property({ type: Boolean, reflect: true, attribute: 'table-content' })
    tableContent = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    draggable = false;
    @property({ type: String, reflect: true, attribute: 'draggable-placeholder' })
    draggablePlaceholder = 'none';
    @property({ type: Boolean, reflect: true, attribute: 'separate-events' })
    separateEvents = true;
    @property({ type: String, attribute: 'color-line' })
    colorLine = '';
    @property({ type: Number, attribute: 'depth', reflect: true })
    depth = 0;
    @property({ type: Boolean, attribute: 'hori-padding' })
    horiPadding = false;
    @property({ type: Boolean, attribute: 'hide-toggle' })
    hideToggle = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'keep-buttons-visible' })
    keepButtonsVisible = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'expand-on-click', reflect: true })
    expandOnClick = false;
    @property({
        type: Object,
        attribute: false,
        hasChanged: () => false,
    })
    dataTransfer?: string | ((index: number) => string);
    @property({
        type: Boolean,
        converter: booleanConverter,
        attribute: 'always-hide-expand-icon',
    })
    alwaysHideExpandIcon = false;

    @state()
    public hiddenIcon;
    @state()
    public hiddenLabel;

    @query('[class$="-right-part"]')
    private _rightContent;

    @state()
    private _hover = false;

    @state()
    private _hoverSlot = false;
    @state()
    private _activeButton = false;

    // Public methods
    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-button'), elem: ['Button'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });

        ListItem.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    // Protected methods

    protected firstUpdated(_changedProperties) {
        this._handleInlineEdit = this._handleInlineEdit.bind(this);
        this.addEventListener('inline-edit-toggle', this._handleInlineEdit);
        this.addEventListener('inline-edit-save', this._handleInlineEdit);
        super.firstUpdated(_changedProperties);
    }

    protected update(changes) {
        if (!this.selected) {
            this.hiddenIcon = this.hiddenIconButtons;
            this.hiddenLabel = this.hiddenButton;
        }
        if (this.selected) {
            this.hiddenIcon = false;
            this.hiddenLabel = false;
        }
        if (this.disabled) {
            this.expanded = false;
        }
        super.update(changes);
    }

    protected updated(changes) {
        this._toggleTabIndex(false);
        this.removeEventListener('click', this._clickHandler);
        this.removeEventListener('keyup', this._keyupHandler);
        if (this.clickable && !this.disabled) {
            this._checkClickableRules(changes);
            this._toggleTabIndex(true);
            this.addEventListener('click', this._clickHandler);
            this.addEventListener('keyup', this._keyupHandler);
        }

        if ((this.hiddenIconButtons || this.hiddenButton) && !this.selected) {
            this.addEventListener('focusin', this._handleFocusOut);
        }
        super.updated(changes);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            clickable: this.clickable,
            draggable: this.draggable,
            selected: this.selected,
            expanded: this.expanded,
            disabled: this.disabled,
            tablecontent: this.tableContent,
            hover: this._hover && !this._hoverSlot,
            focusedIcon: this.hiddenIcon,
            focusedLabel: this.hiddenLabel,
            ['active-row']: this._hover || this._hoverSlot,
            ['active-button']: this._activeButton,
            ['hidden-button']: this.hiddenButton,
            ['hidden-icon-buttons']: this.hiddenIconButtons,
            ['placeholder-before']: this.draggablePlaceholder === 'before',
            ['placeholder-after']: this.draggablePlaceholder === 'after',
            ['side-padding']: this.horiPadding,
            ['hide-toggle']: this.hideToggle,
        });

        const childrenHtml =
            this.hasChildren && !this.expandOnClick
                ? html`
                    <${
                        ListItem.Button.tag
                    } data-testid="listitem-expand" class="${baseClassName}-expand" button-size="small" compact="true"
                      type="tertiary" @mouseenter="${() =>
                          (this._hoverSlot = true)}" @mouseleave="${() => (this._hoverSlot = false)}" @click="${
                          this._toggleListHandler
                      }">
                        <${ListItem.Icon.tag} class="${baseClassName}-icon-arrow" icon=${down} slot="icon-slot"></${
                            ListItem.Icon.tag
                        }>
                    </${ListItem.Button.tag}>`
                : html``;

        const expandHtml =
            this.hasChildren && this.expandOnClick && !this.alwaysHideExpandIcon
                ? html`
                    <${ListItem.Icon.tag} class="${baseClassName}-expand-arrow" icon=${down}></${ListItem.Icon.tag}>`
                : html``;

        return html` <li
            class="${baseClassName} ${rootClasses}"
            ?disabled="${this.disabled}"
            draggable=${ifDefined(this.draggable ? 'true' : undefined)}
            style="--spacing-list-item: ${this.depth * 16}px;"
        >
            <div
                class="${baseClassName}-inner"
                @mouseenter="${this._handleButtonOnmouseover}"
                @mouseleave="${this._handleButtonOnmouseleave}"
            >
                <div class="${baseClassName}-left-part">
                    <div class="${baseClassName}-color-line" style="background-color: ${this.colorLine}"></div>
                    <slot
                        name="${ListItemSlot.CHECKBOX_SLOT}"
                        @mouseenter="${() => (this._hoverSlot = true)}"
                        @mouseleave="${() => (this._hoverSlot = false)}"
                        class="${baseClassName}-checkbox"
                    ></slot>
                    <slot name="${ListItemSlot.ICON_SLOT_BEFORE}" class="${baseClassName}-icon-before"></slot>
                    <slot name="${ListItemSlot.ITEM_TEXT_SLOT}" class="${baseClassName}-item-text"></slot>

                    <slot name="${ListItemSlot.ICON_SLOT_TEXT}" class="${baseClassName}-icon-text"></slot>
                </div>
                <div class="${baseClassName}-right-part">
                    <slot name="${ListItemSlot.METADATA_SLOT}" class="${baseClassName}-metadata"></slot>
                    <slot
                        name="${ListItemSlot.LABEL_BUTTONS_SLOT}"
                        @mouseenter="${() => (this._hoverSlot = true)}"
                        @mouseleave="${() => (this._hoverSlot = false)}"
                        @slotchange="${this._handleSlottedButton}"
                        class="${baseClassName}-buttons-label"
                    ></slot>
                    <div class="${baseClassName}-button-wrapper">
                        <slot
                            name="${ListItemSlot.ICON_BUTTONS_SLOT}"
                            @mouseenter="${() => (this._hoverSlot = true)}"
                            @mouseleave="${() => (this._hoverSlot = false)}"
                            @slotchange="${this._handleSlottedButton}"
                            class="${baseClassName}-buttons-icon"
                        ></slot>
                    </div>
                    ${childrenHtml} ${expandHtml}
                    <slot name="${ListItemSlot.ICON_SLOT_AFTER}" class="${baseClassName}-icon-after"></slot>
                </div>
            </div>
            <ul class="${baseClassName}-children" @click="${this._childEventHandler}">
                <slot class="${baseClassName}-slot" @slotchange=${this._handleSlotChange}></slot>
            </ul>
        </li>`;
    }

    private _checkClickableRules(changes) {
        if (changes.has('expanded') && !this.selected) {
            this.hiddenIcon = this.hiddenIconButtons;
        }
        if (changes.has('selected') && !this.selected) {
            //Would really like to rework tab/selected handling when there's time, a lot of potential bugs
            this._checkHiddenButtons();
        } else if (changes.has('selected') && this.selected) {
            this.hiddenIcon = false;
            this.hiddenLabel = false;
        }
        if (!this._hover) {
            this._checkTabbedButtonChange(changes);
        }
    }

    private _handleFocusOut(e) {
        if (e.target === this && !this.selected) {
            this.dispatchEvent(new CustomEvent('list-item-focus', { bubbles: true, composed: true, detail: this }));
        }
    }

    private _keyupHandler(event): void {
        this._handleFocusEffectOnSafari(true);
        if (
            (event.code === 'Space' || event.code === 'Enter') &&
            this.shadowRoot!.activeElement === this.shadowRoot!.querySelector('li')
        ) {
            this._clickHandler(event);
        }
    }

    private _clickHandler(e): void {
        if (this._clickInButtonSlots(e)) {
            this._handleIconButtonClick(e);
            return;
        }
        if (this.expandOnClick) {
            e.stopPropagation();
            this._handleFocusEffectOnSafari(false);
            this.expanded = !this.expanded;
            this.dispatchEvent(new CustomEvent('list-toggle', { bubbles: true, composed: true, detail: this }));
            return;
        }

        this._handleFocusEffectOnSafari(false);
        this._setSelected();
        if (this.hasChildren && !this.separateEvents) {
            this.expanded = !this.expanded;
        }
        this.dispatchEvent(new CustomEvent('list-item-click', { bubbles: true, composed: true, detail: this }));
    }

    private _handleIconButtonClick(e) {
        e.stopPropagation();
        const button = e.composedPath().find((i) => i.slot === 'icon-buttons-slot');
        if (button) {
            // No nice, but with current construct it could take time for button active state being set,
            // 100 should be slow enough, state is propagated and user still hovering button.
            setTimeout(() => {
                this._activeButton = button.active;
                if (this._activeButton) {
                    this.addEventListener('button-active-toggle', this._handleActiveButton);
                }
            }, 100);
        }
    }

    private _handleActiveButton() {
        this._activeButton = false;
        this.removeEventListener('button-active-toggle', this._handleActiveButton);
    }

    private _toggleListHandler(e): void {
        e.stopPropagation();
        this._handleFocusEffectOnSafari(false);
        this.expanded = !this.expanded;

        if (this.separateEvents) {
            this.dispatchEvent(new CustomEvent('list-toggle', { bubbles: true, composed: true, detail: this }));
        } else {
            this.dispatchEvent(new CustomEvent('list-item-click', { bubbles: true, composed: true, detail: this }));
        }

        // Temp workaround to get notice to size awareness parent elements that size changed. Timeout because transition.
        setTimeout(
            () => this.dispatchEvent(new CustomEvent('resize', { bubbles: true, composed: true, detail: this })),
            300
        );
    }

    private _setSelected() {
        this.dispatchEvent(new CustomEvent('selected-change', { bubbles: true, composed: true, detail: this }));
    }

    private _toggleTabIndex(isEnabled: boolean): void {
        const listElement = this.shadowRoot!.querySelector<HTMLInputElement>('li');
        if (isEnabled) {
            listElement!.setAttribute('tabindex', '0');
        } else {
            listElement!.setAttribute('tabindex', '-1');
        }
    }

    private _handleFocusEffectOnSafari(isEnabled: boolean): void {
        const listElement = this.shadowRoot!.querySelector<HTMLInputElement>('li');

        if (isEnabled) {
            listElement!.classList.add('safari-focus');
        } else {
            listElement!.classList.remove('safari-focus');
        }
    }

    private _handleSlottedButton() {
        if (this.hiddenIconButtons && !this.selected) {
            this._handleHiddenIconButton(true);
        }
        if (this.hiddenButton && !this.selected) {
            this._handleHiddenLabelButton(true);
        }
    }

    private _handleButtonOnmouseover() {
        if (!this.keepButtonsVisible) {
            if (!this.selected) {
                if (this.hiddenButton) {
                    this._handleHiddenLabelButton(false);
                }
            }
            this._hover = true;
        }
    }

    private _handleButtonOnmouseleave() {
        if (!this.keepButtonsVisible) {
            if (!this.selected) {
                if (this.hiddenButton) {
                    this._handleHiddenLabelButton(true);
                }
            }
            this._hover = false;
        }
    }

    private _childEventHandler(e): void {
        if (this._clickInButtonSlots(e)) {
            return;
        }
        e.stopPropagation();
    }

    private _clickInButtonSlots(e) {
        return !!e
            .composedPath()
            .find(
                (i) => i.slot === 'checkbox-slot' || i.slot === 'label-buttons-slot' || i.slot === 'icon-buttons-slot'
            );
    }

    private _handleSlotChange(e): void {
        const target = e.target as HTMLSlotElement;
        if (target.assignedElements().length > 0) {
            this.hasChildren = true;
            target.assignedElements().forEach((f) => f.setAttribute('depth', `${this.depth + 1}`));
        }
    }

    private _checkTabbedButtonChange(changes) {
        if (changes.has('hiddenLabel')) {
            if (!this.hiddenLabel) {
                this._handleHiddenLabelButton(false);
            } else this._handleHiddenLabelButton(true);
        }
        if (changes.has('hiddenIcon')) {
            if (!this.hiddenIcon) {
                this._handleHiddenIconButton(false);
            } else this._handleHiddenIconButton(true);
        }
    }

    private _checkHiddenButtons() {
        if (!this._hover && this.hiddenIconButtons) {
            this.hiddenIcon = true;
        }
        if (this.hiddenIconButtons && this.hiddenIcon) {
            this._handleHiddenIconButton(true);
        }
        if (this.hiddenButton && this.hiddenLabel) {
            this._handleHiddenLabelButton(true);
        }
        this.removeEventListener('focusin', this._handleFocusOut);
    }

    private _handleHiddenIconButton(hide) {
        requestAnimationFrame(() => {
            const tests = this._findIconButtons() || [];
            const animationKeyframes = this._animationKeyFrameFrom(hide);
            const animationOptions = {
                duration: 1,
                iterations: 1,
            };
            tests.forEach((test) => {
                const showAnimation = test.animate ? test.animate(animationKeyframes, animationOptions) : undefined;
                if (showAnimation) {
                    showAnimation.onfinish = () => {};
                }
            });
        });
    }

    private _animationKeyFrameFrom = (hide) =>
        hide ? ListItem.animations['showIcon'].hideAnimation : ListItem.animations['showIcon'].showAnimation;

    private _findIconButtons = () =>
        this._rightContent
            .querySelector('[class$="-buttons-icon"]')
            .assignedElements()
            .filter(
                (el: HTMLElement) =>
                    el.slot === 'icon-buttons-slot' &&
                    (el.tagName === 'BUTTON' || !!el?.shadowRoot?.querySelector('button'))
            ); //Also add filter on tag button to simplify testing

    private _handleHiddenLabelButton(hide) {
        requestAnimationFrame(() => {
            const buttons = this._rightContent.querySelector('[class$="-buttons-label"]');
            const test = buttons.assignedElements().find((el) => el.slot === 'label-buttons-slot');
            const animationKeyframes = hide
                ? ListItem.animations['showLabel'].hideAnimation
                : ListItem.animations['showLabel'].showAnimation;
            const animationOptions = {
                duration: 150,
                iterations: 1,
            };
            if (test) {
                const showAnimation = test.animate ? test.animate(animationKeyframes, animationOptions) : undefined;
                if (showAnimation) {
                    showAnimation.onfinish = () => {
                        test.style.width = hide ? 0 + 'px' : 'fit-content';
                        test.style.opacity = hide ? 0 : 1;
                    };
                }
            }
        });
    }

    private _handleInlineEdit(e) {
        e.stopPropagation();
        const details = e.detail;
        const right = this.shadowRoot!.querySelector('div[class$="-right-part"]') as HTMLDivElement;
        const icon = this.shadowRoot!.querySelector('[class$="-icon-text"]') as HTMLSlotElement;
        if (details.open) {
            right.style.width = '100%';
            requestAnimationFrame(() => {
                right.style.whiteSpace = 'nowrap';
                right.style.transition = 'width 0.15s';
                right.style.width = '0%';
                right.style.overflow = 'hidden';
            });
            if (icon) icon.style.display = 'none';
        } else {
            right.style.display = 'flex';
            right.style.whiteSpace = 'initial';
            right.style.width = 'initial';
            right.style.overflow = 'unset';
            if (icon) icon.style.display = 'initial';
        }
    }
}
