import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';
import { commonStyle } from './commonStyle';

export const listStyles: GenerateStyles = (tagName) => {
    const { root, is, child } = cssSelector(tagName);

    return [
        css`
            :host {
                display: inline-block;
                width: 100%;
            }

            ${root} {
                width: 100%;
                padding: 0;
                margin: 0;
            }

            ${child('loader-wrapper')} {
                display: flex;
                width: calc(100% - 48px);
                height: 100%;
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                gap: 12px;
            }

            ${is('padding')} {
                padding-top: var(--container-inner-spacing-top-bottom, 16px);
                padding-bottom: var(--container-inner-spacing-top-bottom, 16px);
            }

            ${child('li')} {
                width: 100%;
            }
        `,
        commonStyle(false),
    ];
};
