import { css } from '@umetrics/sartorius-ui-base';
import { CSSResult, unsafeCSS } from '@lit/reactive-element';

/*
  CSS generated below is used in the list and ListItem component.
   
   - boolean slot true, the ListItem needs to style the slotted content.
       Like ::slotted([slot='label-buttons-slot'])
       
   - boolean slot false, the List needs to style content of the dynamic slots (id) 
     that are passed to the ListItem component.
       Like slot[name*='label-buttons-slot-']::slotted(*)
       and in above case the slot is named dynamic as for example : label-buttons-slot-1234
 */
export const commonStyle = (slot: boolean): CSSResult[] => {
    const selector = (slotOrName: boolean, id: string): CSSResult =>
        slotOrName ? unsafeCSS(`::slotted([slot='${id}'])`) : unsafeCSS(`slot[name*='${id}-']::slotted(*)`);
    return [
        css`
            ${selector(slot, 'label-buttons-slot')} {
                margin: 0;
                font-size: 14px;
            }

            ${selector(slot, 'icon-buttons-slot')} {
                margin: 0;
                font-size: 16px;
            }

            ${selector(slot, 'checkbox-slot')} {
                margin: 8px 9px;
                font-size: 14px;
            }

            ${selector(slot, 'metadata-slot')} {
                margin: 8px 10px 8px 9px;
                font-size: 12px;
                line-height: 15px;
                white-space: nowrap;
                width: max-content;
            }

            ${selector(slot, 'item-text-slot')} {
                font-size: 14px;
                margin: 0;
                padding-right: 8px;
                white-space: nowrap;
                user-select: none;
                text-overflow: ellipsis;
                overflow: hidden;
            }

            ${selector(slot, 'icon-slot-text')} {
                font-size: 20px;
            }

            ${selector(slot, 'icon-slot-before')} {
                font-size: 24px;
                margin-right: 9px;
            }

            ${selector(slot, 'icon-slot-after')} {
                font-size: 20px;
                transition: transform 0.15s linear;
                margin-right: 10px;
            }
        `,
    ];
};
