export enum ListItemSlot {
    ICON_SLOT_BEFORE = 'icon-slot-before',
    ICON_SLOT_TEXT = 'icon-slot-text',
    ICON_SLOT_AFTER = 'icon-slot-after',
    LABEL_BUTTONS_SLOT = 'label-buttons-slot',
    ICON_BUTTONS_SLOT = 'icon-buttons-slot',
    CHECKBOX_SLOT = 'checkbox-slot',
    METADATA_SLOT = 'metadata-slot',
    ITEM_TEXT_SLOT = 'item-text-slot',
}

export interface ListItemProperties {
    clickable?: boolean;
    selected?: boolean;
    expanded?: boolean;
    disabled?: boolean;
    separateEvents?: boolean;
    keepButtonsVisible?: boolean;
    tableContent?: boolean;
    hiddenButton?: boolean;
    hiddenIconButtons?: boolean;
    colorLine?: string;
    horiPadding?: boolean;
    expandOnClick?: boolean;
    hideToggle?: boolean; // Hide toggle/expand button. Combine with expanded props.
    dataTransfer?: string | ((index: number) => string) /* Could be whatever data that is passed as drag data*/;
    alwaysHideExpandIcon?: boolean;

    /* Emitted events, for reactify users */
    onListItemClick?: any;
    onListToggle?: any;
}
