import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { ListItem } from './listItem';
import { listStyles } from './listStyle';
import { version } from './metadata';
import { Item, ListItems, ListProperties } from './listProperties';
import { perfDebounce, preventResizeObserverError } from '@umetrics/sartorius-ui-utils';
import { ListItemSlot } from './listItemProperties';
import '@lit-labs/virtualizer'; // <lit-virtualizer> is a side effect of this import

@defineElement({ tagName: 'list', styles: listStyles, version })
export class List extends BaseElement implements ListProperties {
    // Static members
    public static DataTestId = 'data-testid';
    private static Loader;
    private static SkeletonRectangle;

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    container; /* Used by picker */
    @property({ type: Boolean, attribute: 'vert-padding', converter: booleanConverter })
    public vertPadding;
    @property({ type: Boolean, attribute: 'hori-padding', converter: booleanConverter })
    public horiPadding;
    @property({ type: String, reflect: true, attribute: 'data-testid' })
    public dataTestId = 'list';
    @property({ type: Boolean, converter: booleanConverter })
    public loaded = true;
    @property({ type: Object, attribute: false })
    public items?: ListItems;

    @state()
    selectedElement: ListItem | null = null;
    @state()
    focusedElement: ListItem | null = null;

    // Private members
    @query('slot')
    private readonly _slot!: HTMLSlotElement;
    private _debouncedCallback;

    // Public methods
    public getDraggables() {
        return this._slot.assignedElements();
    }

    public appendDraggable(element) {
        this.appendChild(element);
    }

    connectedCallback() {
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-loader'), elem: ['Loader'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-skeleton'), elem: ['SkeletonRectangle'] });
        List.subRegister(ListItem);
        List.dynSubRegister(imports, () => {
            super.connectedCallback();
        });
        preventResizeObserverError();
    }

    // Protected methods
    protected firstUpdated(_changedProperties) {
        super.firstUpdated(_changedProperties);
        this._handleSelectedChange = this._handleSelectedChange.bind(this);
        this.addEventListener('list-item-focus', this._handleListItemFocus);
        this.addEventListener('list-toggle', this._handleListItemToggle);
        this.addEventListener('selected-change', this._handleSelectedChange);
    }

    protected updated(changedProperties) {
        super.updated(changedProperties);

        if (changedProperties.has('horiPadding')) {
            this._setListItemProperties();
        }

        if (changedProperties.has('dataTestId')) {
            this._setListItemProperties(changedProperties.get('dataTestId'));
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            padding: this.vertPadding,
        });

        let listItems;
        if (this.items) {
            listItems = this._virtualizeListItems(baseClassName);
        }
        return html`
            <ul class="${rootClasses} ${baseClassName}-ul" @dragstart=${this._handleDragstart}>
                <${List.Loader.tag} loaded="${this.loaded}">
                    <div slot="loader-slot" class="${baseClassName}-loader-wrapper">
                        <${List.SkeletonRectangle.tag} width="100%" height="24px"></${List.SkeletonRectangle.tag}>
                        <${List.SkeletonRectangle.tag} width="100%" height="24px"></${List.SkeletonRectangle.tag}>
                    </div>
                    <slot @slotchange=${this._handleSlotChange}></slot>
                    ${listItems}
                </${List.Loader.tag}>
            </ul>`;
    }

    // Private methods
    private async _handleDragstart(event) {
        const draggedItem = event.target;
        const rect = draggedItem.getBoundingClientRect();
        const index = Array.from(draggedItem.parentElement.children).indexOf(draggedItem);
        event.dataTransfer.setData(
            'text/plain',
            typeof draggedItem.dataTransfer === 'function'
                ? draggedItem.dataTransfer(index)
                : draggedItem.dataTransfer || draggedItem.id || draggedItem.label || index
        );

        const clone = draggedItem.cloneNode(true);
        clone.expanded = false;

        const slottedElements = clone.querySelectorAll(
            '[slot="checkbox-slot"],[slot="checkbox-slot"],[slot="label-buttons-slot"],[slot="icon-buttons-slot"],[slot="icon-slot-after"], :not([slot])'
        );

        slottedElements.forEach((element) => {
            element.parentNode.removeChild(element);
        });

        const maxDim = 300;
        const width = rect?.width < maxDim + 1 ? rect.width : maxDim;
        const height = 36; // Dont know height...
        const offsetX = width / 2;
        const offsetY = height / 2;

        clone.style.position = 'absolute';
        clone.style.top = '-999px';
        clone.style.left = '-999px';
        clone.style.width = `${width}px`;
        clone.style.height = `${height}px`;
        clone.style.background = 'white';
        clone.style.opacity = 1;
        clone.style.transform = 'none';
        clone.style.boxShadow = 'none';
        clone.style.pointerEvents = 'none';
        document.body.appendChild(clone);
        event.dataTransfer.setDragImage(clone, offsetX, offsetY);

        window.setTimeout(() => {
            document.body.removeChild(clone);
        }, 500);
    }

    private _virtualizeListItems(baseClassName) {
        const renderItem = (item: Item, index) => {
            // Only flat option support, in theory presence of opt.option array could yield a group here.
            const checkboxSlotHtml =
                this.items?.hasCheckboxSlot !== false || item.hasCheckboxSlot === true
                    ? html` <slot
                          name="${ListItemSlot.CHECKBOX_SLOT}-${index}"
                          slot="${ListItemSlot.CHECKBOX_SLOT}"
                      ></slot>`
                    : null;
            const labelHtml = item.label
                ? html`<span slot="${ListItemSlot.ITEM_TEXT_SLOT}">${item.label}</span>`
                : html` <slot
                      name="${ListItemSlot.ITEM_TEXT_SLOT}-${index}"
                      slot="${ListItemSlot.ITEM_TEXT_SLOT}"
                  ></slot>`;
            const metadataHtml = item.metadata
                ? html`<span slot="${ListItemSlot.METADATA_SLOT}">${item.metadata}</span>`
                : html` <slot
                      name="${ListItemSlot.METADATA_SLOT}-${index}"
                      slot="${ListItemSlot.METADATA_SLOT}"
                  ></slot>`;

            return html`
                <${ListItem.tag}
                    class="${baseClassName}-li"
                    color-line="${item.colorLine}"
                    ?clickable=${typeof item.clickable === 'function' ? item.clickable(index) : item.clickable}
                    ?hidden-button=${typeof item.hiddenButton === 'function' ? item.hiddenButton(index) : item.hiddenButton}
                    ?hidden-icon-buttons=${typeof item.hiddenIconButtons === 'function' ? item.hiddenIconButtons(index) : item.hiddenIconButtons}
                    ?draggable='${this.items?.draggable === true || (typeof item.draggable === 'function' ? item.draggable(index) : item.draggable)}'
                    .dataTransfer=${typeof item.dataTransfer === 'function' ? item.dataTransfer(index) : item.dataTransfer}
                    ?selected=${typeof item.selected === 'function' ? item.selected(index) : item.selected}
                    @list-item-click=${item.clickCallback}>
                    ${checkboxSlotHtml}
                    <slot name="${ListItemSlot.ICON_SLOT_BEFORE}-${index}"
                          slot="${ListItemSlot.ICON_SLOT_BEFORE}"></slot>
                    <slot name="${ListItemSlot.ICON_SLOT_TEXT}-${index}" slot="${ListItemSlot.ICON_SLOT_TEXT}"></slot>
                    ${labelHtml}

                    ${metadataHtml}
                    <slot name="${ListItemSlot.LABEL_BUTTONS_SLOT}-${index}"
                          slot="${ListItemSlot.LABEL_BUTTONS_SLOT}"></slot>
                    <slot name="${ListItemSlot.ICON_BUTTONS_SLOT}-${index}"
                          slot="${ListItemSlot.ICON_BUTTONS_SLOT}"></slot>
                    <slot name="${ListItemSlot.ICON_SLOT_AFTER}-${index}" slot="${ListItemSlot.ICON_SLOT_AFTER}"></slot>
                </${ListItem.tag}>`;
        };
        return html`
            <lit-virtualizer
                .items=${this.items!.item}
                .renderItem=${renderItem}
                @rangeChanged=${(e: Event & { first; last }) => {
                    // Performance debouncer, do once when browser idle
                    const updateFunc = (first, last) => {
                        this.dispatchEvent(
                            new CustomEvent('list-range-change', {
                                bubbles: true,
                                composed: true,
                                detail: { first, last },
                            })
                        );
                    };
                    if (!this._debouncedCallback) {
                        this._debouncedCallback = perfDebounce(updateFunc, 0);
                    }
                    this._debouncedCallback(e.first, e.last);
                    e.stopPropagation();
                }}
            ></lit-virtualizer>
        `;
    }

    private _handleListItemToggle(e) {
        const target = e.target as ListItem;
        let hasSelectedChild = false;
        const children = Array.prototype.slice.call(target.children);
        const listChildren = children.filter((el) => el instanceof ListItem);
        listChildren.forEach((ele) => {
            if (ele.selected) {
                hasSelectedChild = true;
            }
        });
        if (!target.expanded && hasSelectedChild) {
            target.selected = true;
        } else if (target.expanded && hasSelectedChild) {
            target.selected = false;
        }
    }

    private _handleSelectedChange(e) {
        const target = e.target as ListItem;
        if (this.selectedElement) {
            this.selectedElement.selected = false;
        }
        target.selected = true;
        this.selectedElement = target;
    }

    private _handleSlotChange() {
        // Performance debouncer, do once when browser idle
        if (!this._debouncedCallback) {
            this._debouncedCallback = perfDebounce(() => {
                this._setListItemProperties();
                this._debouncedCallback = undefined;
            }, 'idle');
        }
        this._debouncedCallback();
    }

    private _setListItemProperties(prevDataTestId: string | null = null) {
        const nodes = this._slot ? this._slot.assignedNodes() : [];
        const items: ListItem[] = nodes.filter((n) => n instanceof ListItem);
        const dataTestId = this.getAttribute(List.DataTestId);

        items.forEach((item, index) => {
            item.updateComplete.then(() => {
                item.horiPadding = this.horiPadding;

                const itemDataTestId = item.getAttribute(List.DataTestId);
                if (
                    !itemDataTestId ||
                    itemDataTestId.startsWith(`${dataTestId}-item-`) ||
                    itemDataTestId.startsWith(`${prevDataTestId}-item-`)
                ) {
                    item.setAttribute(List.DataTestId, `${dataTestId}-item-${index}`);
                }
            });
        });
    }

    private _handleListItemFocus(e) {
        if (this.focusedElement && this.focusedElement !== e.target) {
            this.focusedElement.hiddenLabel = true;
            this.focusedElement.hiddenIcon = true;
            this.focusedElement.requestUpdate('hiddenIcon', 'true');
        }
        this.focusedElement = e.detail as ListItem;
        if (this.focusedElement.hiddenIcon && !this.focusedElement.selected) {
            this.focusedElement.hiddenIcon = false;
            this.focusedElement.hiddenLabel = false;
            this.focusedElement.requestUpdate('hiddenIcon', 'false');
        }
    }
}
