import { css, cssSelector, cssVariableAsVar, GenerateStyles } from '@umetrics/sartorius-ui-base';
import { commonStyle } from './commonStyle';

// Some css rules are duplicated in this file. This is due to that html-minifier-terser fail
// when adding same rules one several classes separated by comma.

export const listItemStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        css`
            :host {
                display: block;
            }

            ${is('side-padding')} {
                --spacing-panel: var(--container-inner-spacing-left-right, 24px);
            }

            ${root} {
                display: block;
                width: 100%;
                overflow: hidden;
                outline: 0;
                --spacing-side: var(--spacing-panel, 8px);
            }

            ${root}:focus-visible {
                outline: 2px dashed black;
                outline-offset: -2px;
            }

            ${child('inner')} {
                display: flex;
                align-items: center;
                padding: 0 var(--spacing-side) 0 calc(var(--spacing-side) + var(--spacing-list-item));
                justify-content: space-between;
                height: 36px;
                position: relative;
            }

            ${is('placeholder-before')} ${child('inner')}:before {
                content: '';
                display: block;
                position: absolute;
                width: 100%;
                background: ${cssVariableAsVar('color_secondary_4')};
                height: 4px;
                left: 0;
                top: 0;
            }

            ${is('placeholder-after')} ${child('inner')}:after {
                content: '';
                display: block;
                position: absolute;
                width: 100%;
                background: ${cssVariableAsVar('color_secondary_4')};
                height: 4px;
                left: 0;
                bottom: 0;
            }

            ${child('left-part')} {
                width: 100%;
                overflow: hidden;
            }

            ${child('color-line')} {
                position: absolute;
                margin-top: auto;
                margin-bottom: auto;
                width: 2px;
                height: calc(100% - 4px);
                left: 0;
            }

            ${child('left-part')} {
                display: flex;
                align-items: center;
                height: 100%;
            }

            ${child('right-part')} {
                display: flex;
                align-items: center;
                height: 100%;
                transition: width 0.2s;
                justify-content: right;
            }

            ${child('button-wrapper')} {
                width: auto;
                display: flex;
                opacity: 1;
                align-items: center;
            }

            ${is('selected', 'clickable')} ${child('button-wrapper')} {
                width: auto;
                opacity: 1;
            }

            ${is('active-button', 'clickable')} ${child('button-wrapper')} {
                width: auto;
                opacity: 1;
            }

            ${is('!selected', 'clickable', 'active-row', 'hidden-icon-buttons')} ${child('button-wrapper')} {
                max-width: 200px;
                width: auto;
                opacity: 1;
                transition:
                    max-width 0.75s,
                    opacity 0.1s linear 0.15s;
            }

            ${is(
                '!selected',
                '!active-button',
                'clickable',
                '!active-row',
                'hidden-icon-buttons',
                'focusedIcon'
            )} ${child('button-wrapper')} {
                max-width: 0px;
                width: 0;
                opacity: 0;
                transition: max-width 0.75s;
            }

            ${child('icon-arrow')} {
                font-size: 20px;
                transition: transform 0.15s linear;
            }

            ${child('expand-arrow')} {
                font-size: 20px;
                transition: transform 0.15s linear;
                padding: 8px;
            }

            ${child('checkbox')} {
                --checkbox-input-margin-right: 2px;
            }

            ${child('metadata')} {
                color: ${cssVariableAsVar('color_secondary_10')};
            }

            ${is('!selected', '!disabled', 'clickable', 'hover')} ${child('inner')} {
                cursor: pointer;
                background-color: ${cssVariableAsVar('color_secondary_2')};
                transition: 0.1s;
            }

            ${is('!selected', '!disabled', 'clickable', '!focusedIcon')} ${child('buttons-icon')} {
                --button-text-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${is('!selected', '!disabled', 'clickable', '!focusedLabel')} ${child('buttons-label')} {
                --button-text-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${is('!selected', '!disabled', 'clickable', 'hover')} ${child('inner')}:active {
                cursor: pointer;
                background-color: #00000033;
                transition: 0.2s;
            }

            ${is('selected')} ${child('buttons-label')}:hover {
                --button-hover-text-color: ${cssVariableAsVar('color_primary_2')};
                --button-hover-color: #ffffff26;
                --button-active-color: #ffffff40;
            }

            ${is('selected')} ${child('buttons-icon')}:hover {
                --button-hover-text-color: ${cssVariableAsVar('color_primary_2')};
                --button-hover-color: #ffffff26;
                --button-active-color: #ffffff40;
            }

            ${is('selected')} ${child('expand')}:hover {
                --button-hover-text-color: ${cssVariableAsVar('color_primary_2')};
                --button-hover-color: #ffffff26;
                --button-active-color: #ffffff40;
            }

            ${is('hide-toggle')} ${child('expand')} {
                display: none;
            }

            ${is('selected', 'tablecontent')} {
                background: none;
                color: ${cssVariableAsVar('color_primary_3')};
                --checkbox-border: 1px solid ${cssVariableAsVar('color_primary_3')};
                cursor: pointer;
                font-weight: bold;
            }

            ${is('selected', 'tablecontent')} ${child('metadata')} {
                color: ${cssVariableAsVar('color_primary_3')};
                font-weight: bold;
            }

            ${is('selected')} ${child('inner')} {
                background-color: ${cssVariableAsVar('color_secondary_10')};
                color: ${cssVariableAsVar('color_primary_2')};
                --button-text-color: ${cssVariableAsVar('color_primary_2')};
                --inline-editing-text-color: ${cssVariableAsVar('color_primary_2')};
                --inline-edit-caret-color: ${cssVariableAsVar('color_primary_2')};
                --inline-edit-border-color: 1px solid ${cssVariableAsVar('color_primary_2')};
                --button-hover-text-color: ${cssVariableAsVar('color_primary_2')};
                --button-hover-color: #ffffff26;
                --button-active-color: #ffffff40;
                --button-focus-color: var(--color-primary-2);
            }

            ${is('selected')} ${child('metadata')} {
                color: ${cssVariableAsVar('color_primary_2')};
            }

            ${is('disabled')} ${child('inner')} {
                color: ${cssVariableAsVar('color_secondary_4')};
                --button-text-color: ${cssVariableAsVar('color_secondary_4')};
                --button-hover-text-color: ${cssVariableAsVar('color_secondary_4')};
                --button-hover-color: ${cssVariableAsVar('color_secondary_4')};
                --inline-editing-text-color: ${cssVariableAsVar('color_secondary_4')};
                --inline-edit-caret-color: ${cssVariableAsVar('color_secondary_4')};
                pointer-events: none;
            }

            ${is('disabled')} ${child('metadata')} {
                color: ${cssVariableAsVar('color_secondary_4')};
            }

            ${is('disabled')} ${child('buttons-icon')} {
                --button-text-color: ${cssVariableAsVar('color_secondary_4')};
            }

            ${is('disabled')} ${child('buttons-label')} {
                --button-text-color: ${cssVariableAsVar('color_secondary_4')};
            }

            ${child('children')} {
                overflow: hidden;
                margin: 0;
                padding-left: 0;
                max-height: 0;
                height: auto;
                transition: max-height 0.2s cubic-bezier(0, 1, 0, 1);

                --button-text-color: initial;
                --button-hover-color: initial;
                --button-hover-text-color: initial;
                --button-active-color: initial;
            }

            ${is('expanded')} ${child('children')} {
                max-height: 10000px;
                transition: max-height 1s ease-in-out;
            }

            ${is('!expanded')} ${child('children')} {
                visibility: hidden;
            }

            ${is('expanded')} ${child('children')} {
                background-color: ${cssVariableAsVar('color_primary_2')};
                color: ${cssVariableAsVar('color_primary_3')};
            }

            ${is('expanded')} ${child('icon-arrow')} {
                transform: rotate(180deg);
            }

            ${is('expanded')} ${child('expand-arrow')} {
                transform: rotate(180deg);
            }

            ${is('!selected', '!disabled', 'draggable')} ${child('inner')}:hover {
                cursor: move;
            }

            ${is('hidden-button', 'clickable')} ${child('buttons-label')} {
                --button-text-color: ${cssVariableAsVar('color_primary_2')};
            }

            ${is('hidden-button', 'clickable')}:hover ${child('buttons-label')} {
                --button-text-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${is('hidden-button', 'clickable', 'selected')} ${child('buttons-label')} {
                --button-text-color: ${cssVariableAsVar('color_primary_2')};
            }

            ${is('hidden-button', 'clickable', 'selected', 'tablecontent')} ${child('buttons-label')} {
                --button-text-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${is('hidden-icon-buttons', 'clickable')}:hover ${child('buttons-icon')} {
                --button-text-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${is('hidden-icon-buttons', 'clickable', 'selected')} ${child('buttons-icon')} {
                --button-text-color: ${cssVariableAsVar('color_primary_2')};
            }

            ${is('hidden-icon-buttons', 'clickable', 'selected', 'tablecontent')} ${child('buttons-icon')} {
                --button-text-color: ${cssVariableAsVar('color_primary_3')};
            }

            /* Code working only for Safari */
            @media not all and (min-resolution: 0.001dpcm) {
                @supports (-webkit-appearance: none) {
                    ${root}:active,
                    ${root}:focus {
                        outline: none;
                    }

                    .safari-focus:active,
                    .safari-focus:focus {
                        border: 2px dashed ${cssVariableAsVar('color_primary_3')};
                        margin: -2px;
                    }
                }
            }
        `,
        commonStyle(true),
    ];
};
